  font-family: 'Lato', sans-serif;
  src: local('Lato'), url(./fonts/Lato-Regular.ttf) format('truetype');
}
*/

h1{
    text-align: center;
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    position: relative;
    font-size: 2em;
    font-weight: 300;
    margin: 0 0 30px 0;
    
}

* {
    font-family: Lato;
}
.navbar a{
    display: block;
    padding: 10px;
    color: #ccced3;
    font-size: 1.2em;
}

a:hover{
    text-decoration: none;
    color: #fff;
}

@media only screen and (max-width: 768px){
    .navbar{
        position: fixed;
        display: none;
        height: 100%;
    }
}
.intro{
    text-align: center;
    position: relative;

    height: 100vh;
    min-height: 500px;
    max-height: 1080px;
    background-image: url(/static/media/city-at-night.ac9aebb1.jpg); /* ArtStation - Davey Baker */
    background-size: cover;
    padding: 15px;
    overflow: hidden;

    font-family: Lato;
    color: #fff; 
    font-weight: 700;
    font-size: 5em;

}

#name {
    text-transform: uppercase;
    -webkit-text-decoration-line: none;
            text-decoration-line: none;
    font-weight: 900;
    font-size: 1em;
    letter-spacing: 0.05em;
    line-height: 0.9em;
    margin-top: 20px;

}

#occupation{
    color: #d3d3d3;
    font-weight: 500;
    font-size: 0.7em;
}

#links{
    margin-top: 150px;
}

.btn-rounded{
    display: inline-block;
    color: #fff;
    font-weight: 700;
    padding: 15px 30px;
    border: none;


    -webkit-transition-property: all;


    transition-property: all;
    -webkit-transition-duration: 0.5s;
            transition-duration: 0.5s;
    -webkit-transition-timing-function: ease;
            transition-timing-function: ease;
    -webkit-transition-delay: 0s;
            transition-delay: 0s;

    background: black;
    opacity: 0.7;
}

.btn-rounded:hover {
    background-color: #fff;
    color: black;
    text-decoration: none;
    opacity: 1.0;
}




.about{
    text-align: center;
    padding: 40px 15px;
    border-bottom: 1px solid #dcd9d9;
}


#photo{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 22%;
    height: 22%;
}

#hello{
    margin-top: 15px;
}

.about p{
    text-align: left;
    position: relative;
    display: block;
    min-height: 1px;
    padding-left: 20px;
    padding-right: 20px;
    margin: 0 0 0 30px;
    font-size: 1.1em;
    font-weight: 300;

    color: #74808a;
}

#quote{
    text-align: center;
    font-style: italic;
}


.education{
    padding: 20px 15px 20px 15px;
    border-bottom: 1px solid #dcd9d9;
    background: #f2f2f5;
}

.education-block{
    max-width: 700px;
    margin: 0 auto 30px auto;
    padding: 15px;
    border: 1px solid #dcd9d9;
    text-align: left;
}

.education img{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 40%;
}

.education-block h3{
    font-weight: 500;
    float: left;
    margin: 0;
    color: #374054;
}

.education-block h4{
    color: #74808a;
    clear: both;
    font-weight: 500;
    margin: 0 0 2px 0;
}

.education-block h5{
    font-size: 1.0em;
    font-weight: 300;
    color: #7e8890;
    margin: 0 0 15px 0;
}

.education-block span{
    color: #74808a;
    float: right;
}
h3 {
    display: block;
    font-size: 1.7em;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
}

.education-block p{
    margin: 0;
    color: #74808a;
    font-size: 1em;
}
.experience{
    padding: 30px 0px;
    text-align: center;
    border-bottom: 1px solid #dcd9d9;
    
}

.experience ul{
    display: block;
    margin: 0 auto;
    padding: 0;
    max-width: 95%;
    
}

.experience li{
    display: inline-block;
    margin: 0px;
    padding: 15px 30px;
    list-style: none;
    cursor: default;
}

.experience img{
    vertical-align: middle;
    border: 0;
    text-align: center;
    height: 50%;
    width: 50%;
}
.projects{
    padding: 50px 15px;
    border-bottom: 1px solid #dcd9d9;
    text-align: center;
    background: #f2f2f5;
}

.project{
    position: relative;
    max-width: 900px;
    margin: 0 auto 30px auto;
    overflow: hidden;
    background: #fff;
    border-radius: 4px;
}

.shadow-large{
    box-shadow: 0 3px 6px rgba(0,0,0,0.08), 
                0 3px 6px rgba(0,0,0,0.15);
}

.project-image{
    float: left;
}

.project-info{
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    margin-left: 300px;
    padding: 15px;
}

.project-info h3{
    font-size: 1.5em;
    font-weight: 300;
    color: #374054;
    margin: 0 0 15px 0;
}

.project-info h4{
    font-size: 0.9em;
    margin: -10px 0 10px 0;
    color: #74808a;
    font-weight: 400;
}

.project-info p{
    color: #74808a;
    margin: 10px 10px 15px 10px;
    font-size: 0.9em;
    text-align: justify;
}

.projects a{
    color: #337ab7;
    text-decoration: none;
}

@media only screen and (max-width: 991px){
    .project-image{
        display: none;
    }
    .project-info{
        position: relative;
        margin: 0;
        top: auto;
        -webkit-transform: none;
                transform: none;
    }
}


.activities{
    padding: 50px 15px;
    border-bottom: 1px solid #dcd9d9;
    text-align: center;
    background: #f2f2f5;
}

.activities img{
    height: 50%;
    width: 50%;
}

.activity-header{
    margin-top: 20px;
    border-bottom: 1px solid black;
}

.activity-info{
    margin-top: 10px;
}


.activities p{
    text-align: left;

    font-size: 1em;
    font-weight: 300;
    color: #3e4347;
}

.skills{
    padding: 50px 15px;
    text-align: center;
    border-bottom: 1px solid #dcd9d9;
}

.skills h1{
    margin-bottom: 50px;
}

.skills ul{
    display: block;
    margin: 0 auto;
    padding: 0;
    max-width: 800px;
}

.skills li{
    display: inline-block;
    margin: 7px;
    padding: 5px 10px;
    color: #374055;
    background: #e4e4e4;
    list-style: none;
    cursor: default;
    font-size: 1.2em;
}

